import React from 'react';
import {
  Card,
  CardContent,
  Typography,
  CardMedia,
  makeStyles,
  CardActionArea,
  Theme,
  createStyles,
} from '@material-ui/core';
import { Magazine } from 'src/models';
import { Link } from 'gatsby';
import AnimatedListItem from 'src/components/Animated/AnimatedListItem';

const useStyles = makeStyles((theme: Theme) =>
  createStyles({
    root: {
      display: 'flex',
      flexDirection: 'column',
      justifyContent: 'start',
      alignItems: 'start',
      [theme.breakpoints.up('md')]: {
        flexDirection: 'row',
      },
    },
    image: {
      [theme.breakpoints.up('md')]: {
        maxWidth: 200,
      },
    },
    title: {
      fontWeight: 'bold',
    },
    link: {
      textDecoration: 'none',
    },
  })
);

interface Props {
  magazine: Magazine;
}
const MagazineCard = ({ magazine }: Props) => {
  const {
    fields: { path },
    frontmatter: { title, date, thumbnail },
  } = magazine;

  const style = useStyles();

  return (
    <AnimatedListItem>
      <Link to={path} className={style.link}>
        <Card>
          <CardActionArea className={style.root}>
            <CardMedia className={style.image} component="img" src={thumbnail?.childImageSharp?.fixed.src} />
            <CardContent>
              <Typography component="h3" variant="subtitle1" className={style.title}>
                {title}
              </Typography>
              <Typography variant="caption">{date}</Typography>
            </CardContent>
          </CardActionArea>
        </Card>
      </Link>
    </AnimatedListItem>
  );
};

export default MagazineCard;
