import React from 'react';
import Page from 'components/Layout/Page';
import { graphql } from 'gatsby';
import { Container, Grid } from '@material-ui/core';
import { Magazine } from 'src/models';
import MagazineCard from 'components/Cards/MagazineCard';

export const pageQuery = graphql`
  query {
    allMdx(sort: { order: DESC, fields: [frontmatter___date] }, filter: { frontmatter: { draft: { ne: true } } }) {
      edges {
        node {
          id
          fields {
            path
          }
          frontmatter {
            date(formatString: "MMMM DD, YYYY")
            title
            thumbnail {
              childImageSharp {
                fixed(width: 640, height: 640, quality: 100, cropFocus: CENTER) {
                  ...GatsbyImageSharpFixed
                }
              }
            }
          }
        }
      }
    }
  }
`;

interface Props {
  data: {
    allMdx: {
      edges: Array<{ node: Magazine }>;
    };
  };
}
const Magazines = ({
  data: {
    allMdx: { edges },
  },
}: Props) => {
  const magazineList = edges.filter(edge => Boolean(edge.node.frontmatter.date));
  return (
    <Page pageName="Magazines" description="루비콘의 에피소드가 담긴 포스팅입니다" showTitle={true}>
      <Container>
        <Grid container spacing={3}>
          {magazineList.map(({ node }) => (
            <Grid key={node.id} xs={12} sm={12} md={12} lg={12} item>
              <MagazineCard magazine={node} />
            </Grid>
          ))}
        </Grid>
      </Container>
    </Page>
  );
};
export default Magazines;
